/* Assuming this is your CSS file */

/* Reset some default styles for consistency */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f9f9f9;
}

.neuro-lines {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0; /* Set a low z-index so it's just above the AnimatedBackground */
  overflow: hidden; /* Hide the overflow to prevent scrollbars */
  pointer-events: none; /* Ignore pointer events on the neuro-lines container */
}

/* Add styles for the container of other elements */
/* Add styles for the PostList component */
.post-list {
  display: flex; /* Use flexbox to stack the post containers vertically */
  flex-wrap: wrap; /* Allow wrapping if there are more posts */
  gap: 20px;
  padding: 20px;
  flex-direction: column; /* Stack the posts vertically */
}

.post {
  border-radius: 5px;
  position: relative; /* Add position relative to create a stacking context */
  display: flex; /* Use flexbox to align image and content */
  align-items: flex-start; /* Align items to the start (left) of the container */
}

.post img {
  width: auto; /* Set the width for the image */
  height: auto; /* Let the height adjust automatically based on the aspect ratio */
  border-radius: 5px; /* Apply border-radius to all corners */
  object-fit: cover; /* Crop the image to fit the container */
}

.post-content {
  flex: 1; /* Allow the content to grow and fill the available space */
  padding: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.post h2 {
  margin-top: 10px;
  font-size: 24px;
}

.post p {
  margin-top: 10px;
}

.transparent-h2 {
  background-color: transparent;
}

.post.home {
  display: flex;
  align-items: center; /* Vertically center the content */
  justify-content: center; /* Horizontally center the content */
  height: 100%; /* Ensure the container takes the full height */
}

