/* ... Other styles ... */

/* Add styles for the AnimatedBackground component */
.animated-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradientAnimation 15s ease infinite;
  z-index: -1; /* Set a negative z-index to keep it at the back */
}
/* Add styles for the Gallery component */
/* ... */

